import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent {
  username: string = '';
  password: string = '';

  constructor(private http: HttpClient, private router: Router) {}

  onSubmit() {
    const payload = {
      username: this.username,
      password: this.password,
    };

    this.http.post<any>('https://tmpro-5265168c0988.herokuapp.com/api/auth/login', payload).subscribe(
      (response) => {
        // Almacena el token en el almacenamiento local
        localStorage.setItem('token', response.token);

        // Redirige al usuario al dashboard tras el inicio de sesión exitoso
        this.router.navigate(['/dashboard']);
      },
      (error) => {
        // Maneja errores de inicio de sesión
        console.error('Error al iniciar sesión:', error);
        alert('Error al iniciar sesión. Por favor, verifica tus credenciales.');
      }
    );
  }

  navigateToRegister() {
    // Método para redirigir al formulario de registro
    this.router.navigate(['/register']);
  }
}
